.App {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: #f5f7fa;
  font-family: 'Roboto', 'Segoe UI', sans-serif;
  padding: 30px 0;
  overflow-x: hidden; /* Prevent horizontal scrolling */
}

.container {
  max-width: 800px;
  width: 95%;
  padding: 2rem;
  background-color: white;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  min-height: 700px;
  overflow: hidden; /* Prevent content from overflowing */
}

h1 {
  color: #ff4757;
  margin-bottom: 1.5rem;
}

h2 {
  color: #2f3542;
  margin: 1.5rem 0;
}

.card-section {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-bottom: 40px;
  position: relative;
  min-height: 330px; /* Increased to match the CardContainer height */
  overflow: hidden; /* Prevent content from overflowing */
  padding: 15px; /* Add padding to make room for the shadow */
}

/* Card styling is now handled by styled-components in App.tsx */
.card p {
  font-size: 1.2rem;
  line-height: 1.6;
  margin: 0;
  text-align: left;
  width: 100%;
}

.controls-section {
  margin-top: 40px;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 200px;
}

p {
  color: #333;
  font-size: 1.1rem;
  line-height: 1.5;
}

.instructions {
  color: #666;
  font-style: italic;
  margin: 1.5rem 0;
}

small {
  font-size: 0.8rem;
  color: #999;
  display: block;
  margin-top: 5px;
}

.result-item {
  padding: 10px 0;
  border-bottom: 1px solid #eee;
}

.result-item:last-child {
  border-bottom: none;
}

.export-button {
  background-color: #3498db;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  font-size: 1rem;
  cursor: pointer;
  margin-top: 20px;
  transition: background-color 0.3s ease;
}

.export-button:hover {
  background-color: #2980b9;
}

/* Keyboard hint styles */
.keyboard-hint {
  margin-top: 25px;
  font-size: 0.9rem;
  color: #888;
  display: flex;
  justify-content: center;
  align-items: center;
}

.keyboard-hint kbd {
  background-color: #f8f9fa;
  border: 1px solid #d3d3d3;
  border-radius: 3px;
  box-shadow: 0 1px 1px rgba(0,0,0,.2);
  color: #333;
  display: inline-block;
  font-size: 0.8rem;
  font-weight: 700;
  line-height: 1;
  padding: 3px 6px;
  margin: 0 5px;
  white-space: nowrap;
}

/* Add overflow control to the body and html */
body, html {
  overflow-x: hidden;
  width: 100%;
  margin: 0;
  padding: 0;
}
